/* work-sans-100normal - latin */
@font-face {
  font-family: 'Work Sans';
  font-style: normal;
  font-weight: 100;
  src: url('./files/work-sans-latin-100.eot'); /* IE9 Compat Modes */
  src:
    local('Work Sans Thin '),
    local('Work Sans-Thin'),
    url('./files/work-sans-latin-100.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('./files/work-sans-latin-100.woff2') format('woff2'), /* Super Modern Browsers */
    url('./files/work-sans-latin-100.woff') format('woff'), /* Modern Browsers */
    url('./files/work-sans-latin-100.svg#work-sans') format('svg'); /* Legacy iOS */
}

