/* space-mono-400normal - latin */
@font-face {
  font-family: 'Space Mono';
  font-style: normal;
  font-weight: 400;
  src: url('./files/space-mono-latin-400.eot'); /* IE9 Compat Modes */
  src:
    local('Space Mono Regular '),
    local('Space Mono-Regular'),
    url('./files/space-mono-latin-400.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('./files/space-mono-latin-400.woff2') format('woff2'), /* Super Modern Browsers */
    url('./files/space-mono-latin-400.woff') format('woff'), /* Modern Browsers */
    url('./files/space-mono-latin-400.svg#space-mono') format('svg'); /* Legacy iOS */
}

